<template>
	<div>
		<header>
			<div class="location">
				<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 26 31" class="index-okfdP_0">
					<path fill="#FFF" fill-rule="evenodd" d="M22.116 22.601c-2.329 2.804-7.669 7.827-7.669 7.827-.799.762-2.094.763-2.897-.008 0 0-5.26-4.97-7.643-7.796C1.524 19.8 0 16.89 0 13.194 0 5.908 5.82 0 13 0s13 5.907 13 13.195c0 3.682-1.554 6.602-3.884 9.406zM18 13a5 5 0 1 0-10 0 5 5 0 0 0 10 0z">
					
					</path>
				</svg>
				<span>河北软件职业技术学院</span>
				<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 14 8" class="index-2iXz3_0">
					<path fill="#FFF" fill-rule="evenodd" d="M5.588 6.588c.78.78 2.04.784 2.824 0l5.176-5.176c.78-.78.517-1.412-.582-1.412H.994C-.107 0-.372.628.412 1.412l5.176 5.176z"></path>
				</svg>
			</div>
		</header>
		<section class="search">
			<div class="button">
				<a href="">
					<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16">
						<path fill-opacity=".38" d="M14.778 13.732a.739.739 0 1 1-1.056 1.036l-2.515-2.565a.864.864 0 0 1-.01-1.206 4.894 4.894 0 0 0 1.357-3.651c-.126-2.492-2.156-4.52-4.648-4.647a4.911 4.911 0 0 0-5.16 5.163c.126 2.475 2.13 4.496 4.605 4.642.451.026.896-.008 1.326-.1a.739.739 0 0 1 .308 1.446c-.56.12-1.137.164-1.72.13-3.227-.19-5.83-2.815-5.995-6.042a6.39 6.39 0 0 1 6.71-6.715c3.25.165 5.884 2.8 6.05 6.048a6.37 6.37 0 0 1-1.374 4.3l2.12 2.161z"></path>
					</svg>
					<span>搜索饿了么商家、商品名称</span>
				</a>
			</div>
		</section>
		<!-- 热门东西数据 -->
		<section class="hot-search"> 
			<ul>
				<li v-for="(item,index) in hotsearches" :key="index">{{item.word}}</li>
			</ul>
		</section>

		<!-- 食品分类轮播图 -->
		<section class="entries">
			<mt-swipe :auto="4000">
				<mt-swipe-item v-for="(item,index) in entries" :key="index">
					<a href="" v-for="(item2,index2) in item" :key="index2">
						<img :src="item2.image_hash | imgUrl" alt="">
						<span>{{item2.name}}</span>
					</a>
				</mt-swipe-item>
			</mt-swipe>
		</section>

		<!-- 推荐商家 -->
		<section class="recommends">
			<!-- 标题 -->
			<section class="shop-title">
				<h3>推荐商家</h3>
			</section>
			<!-- 商家列表 -->
			<section class="shop-list" v-for="(item,index) in restaurants" :key="index">
				<div class="shop">
					<!-- 左商家logo -->
					<div class="shop-logo" >
						<img :src="item.restaurant.image_path|imgUrl">
					</div>
					<!-- 右商家信息 -->
					<div class="shop-main">
						<!-- 商家名字 -->
						<div class="title">
							<h3>{{item.restaurant.name}}</h3>
						</div>
						<!-- 商家评价 -->
						<div class="rating">
							<div class="pj">
								<span><elm-star :score="item.restaurant.rating" :size="24"></elm-star></span>
								<span>{{item.restaurant.rating}}</span>
								<span>|</span>
								<span>{{item.restaurant.recent_order_num}}单</span>
							</div>
							<div class="fn">
								<span>蜂鸟专送</span>
							</div>
						</div>
						
						<!-- 配送费，时间，距离 -->
						<div class="fee">
							<div class="deliver-fee">
								<span>¥{{item.restaurant.float_minimum_order_amount}}起送</span>
								<span>|</span>
								<span>配送费¥{{item.restaurant.delivery_fee_discount}}</span>
							</div>
							<div class="time-fee">
								<span>{{item.restaurant.distance / 1000|toFix}}km</span>
								<span>{{item.restaurant.order_lead_time}}分钟</span>
							</div>
						</div>
						<!-- 口碑人气 -->
						<div class="tag-line">
							<span>奶茶果汁</span>
							<span>
								<img src="" alt="">
								<span>口碑人气好店</span>
							</span>
						</div>
						<!-- 活动 -->
						<div class="activity">
							<div class="activity-list">
							<div class="activity1" v-for="(item,index) in item.restaurant.activities" :key="index">
								<span style="background-color: rgb(240, 115, 115);">{{item.icon_name}}</span>
								<span>{{item.tips}}</span>
							</div>
							</div>
							<div class="activity-btn">
								<span>{{item.restaurant.activities.length}}活动</span>
							</div>
						</div> 
						

					</div>
				</div>
			</section>
		</section>
		<elm-tabbar></elm-tabbar>
	</div>
</template>

<script>
	import Tabbar from "@/components/footer/Tabbar"
	import {getHotSearches,getEntries,getRestaurants} from '@/api/msite'
	import Star from '@/components/star/Star'
	
	//导入lodash插件
	import _ from 'lodash'
	import {Swipe,SwipeItem} from 'mint-ui';

	import 'mint-ui/lib/style.css'
	import 'mint-ui/lib/index.js'
	import Vue from 'vue'
	Vue.component(Swipe.name,Swipe);
	Vue.component(SwipeItem.name,SwipeItem);
	export default{
		components:{
			"elm-tabbar":Tabbar,
			"elm-star":Star
		},
		data(){
			return{
				hotsearches:[],
				entries:[],
				restaurants:[]
			}
		},
		//过滤器
		filters:{
			imgUrl(src){
				let url = "https://fuss10.elemecdn.com/";

				let reg = /jpeg|jpg|png|gif$/;
				let res = src.match(reg);
				console.log(res[0]);
				url += src.slice(0,1)+"/"+src.slice(1,3)+"/"+src.slice(3)+"."+res[0];
				url += "?imageMogr/format/webp/thumbnail/!90x90r/gravity/Center/crop/90x90/";
				return url;
			},
			//保留2位小数
			toFix(data){
				return data.toFixed(2)
			}
		},
		created(){
			//获取热门数据
			getHotSearches()
			.then((data) =>{
				
				this.hotsearches=data.data
				//console.log(this.hotsearches)
			})
			.catch((err) =>{
				console.log(err);
			})

			//获取分类轮播图数据
			getEntries()
			.then((data) =>{
				//console.log(data.data[0].entries)
				this.entries = _.chunk(data.data[0].entries,8);
				//console.log(this.entries)
			})
			.catch((err) =>{
				console.log(err)
			})

			//获取商家列表
			getRestaurants()
			.then((data) =>{
				this.restaurants = data.data.items
				console.log(this.restaurants)
			})
			.catch((err) =>{
				console.log(err)
			})
		}

	}
</script>

<style lang="less" scoped>
	*{box-sizing: border-box}
	
	header{
		height:1.3333333rem ;
		background-image: linear-gradient(90deg,#0af,#0085ff);
		display: flex;
		align-items: center;
		padding-left: 0.346667rem;
		.location{
			display: flex;
			align-items: center;
			.index-okfdP_0{
				width: 0.4rem;
				height: 0.426667rem;
			}
			.index-2iXz3_0{
				width: 0.186667rem;
				height: 0.106667rem;
			}
			span{
				font-size: 0.453333rem;
				color: #fff;
				width: 90%;
				white-space: nowrap;
				overflow: hidden;
				text-overflow: ellipsis;
				margin-left: 0.133333rem;
				font-weight: 700;
			}
		}
	}
	.search{
		width: 100%;
		background-image: linear-gradient(90deg,#0af,#0085ff);
		padding: 0 0.346667rem 0.346667rem;
		display: flex;
		align-items: center;
		justify-content: center;
		.button{
			width: 100%;
			height: 0.986667rem;
			line-height: 0.986667rem;
			background: #fff;
			a{
				display: flex;
				align-items: center;
				justify-content: center;
				text-decoration: none;
				svg{
					width: 0.386667rem;
					height: 0.386667rem;
				}
				span{
					font-size: 0.373333rem;
					color: #999;
					margin-left: 0.16rem;
				}
			}
		}
	}
	.hot-search{
		height: 1.12rem;
		background-image: linear-gradient(90deg,#0af,#0085ff);
		width: 100%;
		padding: 0.0rem 0.346667rem;
		ul{
			width: 100%;
			overflow-x: auto;
			white-space: nowrap;
			&::-webkit-scrollbar{
				display: none;
			}
			li{
				font-size: 0.32rem;
				display: inline-block;
				color: #fff;
				&:not(:last-child){
					margin-right: 0.48rem;
				}
			}
		}
	}
	.entries{
		height: 4.533333rem;
		a{
			display: flex;
			flex-direction: column;
			align-items: center;
			width: 25%;
			float: left;
			text-decoration: none;
			margin-top: 0.373333rem;
			img{
				width: 0.853333rem;
				height: 0.85333rem;
			}
			span{
				font-size: 0.32rem;
				color: #666;
				margin-top: 0.266667rem;
			}
		}
	}
	// 推荐商家
	.recommends{
		.shop-title{
			h3{
				font-size: .4rem;
				height: 1.2rem;
				display: flex;
				justify-content: center;//左右居中
				align-items: center;//上下居中
				&:before,&:after{
					content: "";
					display: block;
					width: .53333rem;
					height: .026667rem;
					background-color: #999;
				}
				&:before{
					margin-right: .346667rem;
				}
				&:after{
					margin-left: .346667rem;

				}
			}
		}
		.shop-list{
			padding: .266667rem;
			.shop{
				font-family: 微软雅黑;
				display: flex;
				justify-content: space-between;
				ustify-content: flex-start;

				.shop-logo{
					img{
						width: 2.666667rem;
					}
				}
				.shop-main{
					display: flex;
					flex-direction: column;
					justify-content: space-between;
					flex-grow: 1;//撑开
					.title{
						height:.533333rem;
						span{
							font-size: .4rem;
							display: flex;
						}
						h3{
							font-size: .4rem;
							font-weight: 700;
							display: flex;
						}
					}
					.rating{
						margin-top:.16rem;
						height:.4rem;
						display:flex;
						justify-content: space-between;
						span{
								font-size: .36rem;
							}
						.pj{
							display: flex;
						}
						.fn{
							display: flex;
							span{
								background-image: linear-gradient(45deg,#0085ff,#0af);
								color: #fff;
							}
						}
					}
					.fee{
						display: flex;
						justify-content: space-between;
						span{
							font-size: .36rem;
						}
						
					}
					.tag-line{
						display: flex;
						justify-content: space-between;
						span{
							font-size: .36rem;
						}
					}
					.activity{
						display: flex;
						justify-content: space-between;
						span{
							font-size: .32rem;
						}
					}
				
			}	
		}
	}
}
</style>


